import React from "react";
import {Button, Form, Modal} from "antd";
import printJS from 'print-js'
import {BASE_URL} from "../../../axios/config";

const CodeModal = Form.create()(props => {
    const {visible, handleCancel, current} = props;
    // pedestral

    // 打印
    const handleType = () => {
        printJS({printable: [`${BASE_URL}api/ibf/${current.type}/qrCode?ids=${current.id}`], documentTitle:`${current.bridgeName}_${current.name}.png`,type: 'image', imageStyle: 'width:50%'});
    }
    // 导出
    const handleExport = () => {
        const aLink = document.createElement('a');
        document.body.appendChild(aLink);
        aLink.style.display = 'none';
        aLink.download = `${current.bridgeName}_${current.name}.png`;
        aLink.href = `${BASE_URL}api/ibf/${current.type}/qrCode?ids=${current.id}`;
        aLink.click();
        document.body.removeChild(aLink);
    }
    return (
        <Modal
            maskClosable={false}
            title="查看二维码"
            width={300}
            visible={visible}
            wrapClassName="codeModal"
            destroyOnClose
            onCancel={handleCancel}
            footer={[
                // 定义右下角 按钮的地方 可根据需要使用 一个或者 2个按钮
                <Button onClick={() => handleType()}>
                    打印
                </Button>,
                <Button onClick={() => handleExport()}>导出</Button>
            ]}
        >
            <div id="printArea">
                <img src={`${BASE_URL}api/ibf/${current.type}/qrCode?ids=${current.id}`} alt="梁场二维码" width={120} height={120}/>
            </div>
            <p>{current.bridgeName}_{current.name}</p>
        </Modal>
    )
})

export default CodeModal;
