import styled from 'styled-components';
import loginLogo from '../../../static/imgs/icon.png';
import disable from '../../../static/imgs/disable.png';
import next from '../../../static/imgs/next.png';
import reload from '../../../static/imgs/reload.png';
export const Layout = styled.div`
  .substr{
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    display: inline-block;
    height: 26px;
    padding-left: 15px;
  }
  .layout-header{
      margin:15px 0;
      .ant-form-item{
          margin-right:0;
      }
  }
  .search {
      float: right;
      height: 36px;
      line-height: 36px;
  }
  .qrcode {
      width: 15px;
      height: 15px;
      display: inline-block;
      cursor: pointer;
      vertical-align: middle;
  }
  
  .edit,.edit-exit,.no-exit {
      width: 24px;
      height: 24px;
      display: inline-block;
      cursor: pointer;
      vertical-align: middle;
  }
  .qrcode {
      background: url(${loginLogo}) no-repeat -456px -32px;
  }
  .edit {
      background: url(${next}) no-repeat 0 0;
  }
  .no-exit {
      background: url(${disable}) no-repeat 0 0;
  }
  
  .edit-exit {
      background: url(${loginLogo}) no-repeat -401px -27px;
  }
  .selectTime {
      width: 200px;
      
  }
  
  .tableStyle .ant-table{
      overflow-x: auto;
  }
  
  td span {
      cursor: pointer;
  }
  
  //.tableStyle .ant-table-content {
  //    min-width: 1800px;
  //    overflow-x: auto;
  //}
.react-resizable {
  position: relative;
  background-clip: padding-box;
}

.react-resizable-handle {
  position: absolute;
  width: 10px;
  height: 100%;
  bottom: 0;
  right: -5px;
  cursor: col-resize;
  z-index: 1;
}
.bim-reload {
    width: 20px;
    height: 20px;
    float: left;
    background-size: 100%;
    background-color:red;
    <!-- background:url(${reload}) no-repeat 0 0 ; -->
}
.bim-reload {
    background: url(${loginLogo}) no-repeat -401px -27px;
}
`;

export const TextAreaSpan=styled.div`
    position: absolute;
    bottom: -17px;
    right: 10px;

`
