import React, {PureComponent} from 'react';

export default class LineWrap extends PureComponent {

    render() {
        const {title} = this.props;
        return (
        <span className="col-span" title={title} style={{WebkitLineClamp: 1}}>
          {title}
        </span>
        )
    }
}