import styled from 'styled-components';
import loginLogo from '../../../static/imgs/icon.png';
import reload from '../../../static/imgs/reload.png';
export const Layout = styled.div `
  .layout-header{
      margin:15px 0;
      .ant-form-item{
          margin-right:0px;
      }
      .ant-btn-primary+.ant-btn-primary{
          margin-left:20px;
      }
  }
  .qrcode,.edit {
      width: 15px;
      height: 15px;
      display: inline-block;
      cursor: pointer;
  }
  .button-icon{
    width: auto;
    padding: 0 6px;
    border: none;
    background: transparent;
  }
  .qrcode {
      background: url(${loginLogo}) no-repeat -456px -32px;
  }
  .edit {
      background: url(${loginLogo}) no-repeat -475px -34px;
  }
  .bim-reload {
    width: 20px;
    height: 24px;
    background-color:red;
    vertical-align: middle;
    display: inline-block;
    background:url(${reload}) no-repeat 0 0 ;
    background-size: 77%;
 }
`