/**
 * 台座設置
 */
import React, {PureComponent} from "react";
import {connect} from "react-redux";
import {Button, Col, Form, Input, message, Modal, Row, Select, Table,Icon} from 'antd';
import {Layout} from "./style";
import *  as api from "../../../axios";
import CodeModal from './modalQrcode'
import {BASE_URL} from "../../../axios/config";
import {TextAreaSpan} from "../progressManagement/style";
import LineWrap from '../../common/lineWrap';
import {toJS} from 'immutable'
import {actionCreators} from "../../common/store"
import { set } from "immutable";
import "./resize.css"
import ResizeableTitle from "../../common/resizable";
import AuthComponent from "../../common/authComponent ";
const AuthButton = AuthComponent(Button);
const {Option} = Select;
const FormItem = Form.Item;
const {TextArea} = Input;
const {confirm} = Modal;
//新增 编辑弹窗
const ModalForm = Form.create()(props => {
    const {form, title, visible, handleCancel, current, areas, handleInputValue, textAreaValue,setValue} = props;
    let historyInfos = props.historyInfos.toJS?props.historyInfos.toJS():props.historyInfos
    const {getFieldDecorator,getFieldsValue} = form;
    const isEdit = Object.keys(current).length !== 0;
    const handleChange = (value,option) => {
        if(typeof value == 'string') {
            setValue({areaId:value,stage:historyInfos.stage});
        }else{
            setValue({areaId:historyInfos.areaId,stage:value});
        }
    }
    const handleOk = () => {
        form.validateFields((err, fieldsValue) => {
            if(err)return
            //编辑状态下 需要传台座id
            isEdit && (fieldsValue['id'] = current.id);
            api.createPedestral(fieldsValue).then(res => {
                if (res.code === 1) {
                    message.success(isEdit ? '编辑成功' : '添加成功');
                    handleCancel()
                } else {
                    message.warning(res.msg);
                }
            })
        });
    };
    return (
        <Modal
            maskClosable={false}
            width={500}
            title={title}
            visible={visible}
            destroyOnClose
            onCancel={handleCancel}
            className="plat-setting"
            footer={[
                <Button key='ok' type="primary" onClick={handleOk}>
                    确定
                </Button>,
                <Button key='cancle' onClick={handleCancel}>
                    取消
                </Button>,
            ]}
        >
            <Form onSubmit={handleOk} layout="inline" hideRequiredMark={false}>
                <Row>
                    <Col span={24} style={{
                        height: 60
                    }}>
                        <FormItem label="台座分区：">
                            {getFieldDecorator('areaId', {
                                initialValue: isEdit ? areas&&areas.find(area => area.v == current.areaName).k : historyInfos.areaId?historyInfos.areaId:(areas&&areas[0].k),
                            })(
                                <Select
                                    placeholder="请选择"
                                    onChange = {handleChange}
                                    style={{
                                        width: 365
                                    }}>
                                    {
                                        areas&&areas.map(item => (<Option value={item.k} key={item.v}>{item.v}</Option>))
                                    }
                                </Select>
                            )}
                        </FormItem>
                    </Col>
                    <Col span={24} style={{
                        height: 60
                    }}>
                        <FormItem label="台座类型：">
                            {getFieldDecorator('stage', {
                                initialValue: isEdit ? historyInfos.stage!==0?historyInfos.stage:current.stage :historyInfos.stage!==0?historyInfos.stage:1,
                            })(
                                <Select
                                    placeholder="请选择"
                                    onChange = {handleChange}
                                    style={{
                                        width: 365
                                    }}>
                                    {/* 1：钢筋绑扎台座；10：制梁台座；20： 存梁台座 */}
                                    <Option value={1}>钢筋绑扎台座</Option>
                                    <Option value={10}>制梁台座</Option>
                                    <Option value={20}>存梁台座</Option>
                                </Select>
                            )}
                        </FormItem>
                    </Col>
                    <Col span={24} style={{
                        height: 80
                    }}>
                        {!isEdit?<FormItem label="台座名称：">
                            {getFieldDecorator('name', {
                                initialValue: isEdit ? current.name : '',
                                rules: [
                                    {
                                        required: true,
                                        message: "请输入台座名称"
                                    },
                                ]
                            })(
                                <TextArea style={{
                                    width: 365
                                }} maxLength="200" autosize={{minRows: 3, maxRows: 3}} onChange={handleInputValue}
                                          >
                                </TextArea>
                            )}
                              <TextAreaSpan>({textAreaValue.length}/200)</TextAreaSpan>
                        </FormItem>:<FormItem label="台座名称：">
                            {getFieldDecorator('name', {
                                initialValue: isEdit ? (current.name.length>20?current.name.substr(0,20):current.name) : '',
                                rules: [
                                    {
                                        required: true,
                                        message: "请输入台座名称"
                                    },
                                ]
                            })(
                                <Input style={{
                                    width: 365,
                                    paddingRight:54,
                                }} maxLength={20}  onChange={handleInputValue}/>
                                
                            )}
                              <TextAreaSpan >({(textAreaValue?textAreaValue:(current.name.length>20?current.name.substr(0,20):current.name)).length}/20)</TextAreaSpan>
                        </FormItem>}
                    </Col>
                </Row>
            </Form>
        </Modal>
    );
})

class PlatformSetting extends PureComponent {
    state = {
        page: 1,
        size: 20,
        totalCount: 0,
        current: {},
        visible: false,
        title: '',
        selectedRowKeys: [],
        selectCheckList:[],
        beamInfo:[],
        loading: false,
        codeVisible: false,
        textAreaValue: '',
        columns : [
            {
                title: '序号',
                key: 'id',
                width: 50,
                render: (text, record, index) => index + 1
            }, {
                title: '台座分区',
                width: 180,
                dataIndex: 'areaName',
                onCell: () => {
                    return {
                      style: {
                        maxWidth: 10,
                      }
                    }
                },
                render:(text,record) =>(<div className="substr"  title={text}>{text}</div>)

            }, {
                title: '台座类型',
                width: 100,
                dataIndex: 'stageName',
                onCell: () => {
                    return {
                      style: {
                        maxWidth: 10,
                      }
                    }
                },
                render:(text,record) =>(<div className="substr"  title={text}>{text}</div>)
            }, {
                title: '台座名称',
                width: 170,
                dataIndex: 'name',
                onCell: () => {
                    return {
                      style: {
                        maxWidth: 10,
                      }
                    }
                },
                render:(text,record) =>(<div className="substr"  title={text}>{text}</div>)
            }, {
                title: '关联BIM',
                width: 100,
                dataIndex: 'handle',
                //列头图标
               /*  filterDropdown: (<div></div>),
                filterIcon: <div>
                    <i className="bim-reload" onClick={ this.bimModelrefresh} ></i>
                </div>, */
                render: (text) =>
                    <LineWrap title={text}/>
            }, {
                title: '二维码',
                dataIndex: 'code',
                // align: 'center',
                width: 80,
                render: (text, record) => (<i className="qrcode" onClick={() => this.handleCode(record)}/>)
            }, {
                title: '操作',
                dataIndex: 'operation',
                width: 80,
                // align: 'center',
                // width: 100,
                render: (text, record) => (record.hasData ?
                    <i className="edit" title="编辑" style={{cursor: 'not-allowed'}}></i>
                    :
                    <AuthButton className="button-icon" source ="fun" authcode ="77005006" authcodes={(this.props.authCode.toJS()).funAuthCode} onClick={() => this.handleEdit(record)}><i className="edit" title="编辑" /></AuthButton>)
            }
        ]
    };
    handleCancel = () => {
        this.setState({visible: false, codeVisible: false, textAreaValue: ''});
        this.getPedestral()
    };
    handleEdit = async item => {
        const {data: { data }} = await api.getUserInfo(localStorage.getItem('beamField'));
        this.setState({
            beamInfo:data,
            visible: true,
            current: item,
            title: '编辑台座',
        });
    };
    handleInputValue = (e) => {
        this.setState({
            textAreaValue: e.target.value
        })
    }
    // 二維碼
    handleCode = item => {
        if (item.id) {
            this.setState({
                codeVisible: true,
                current: {...item,type:"pedestral"}
            });
        }
    };

    // 批量导出二维码
    batchExport = () => {
        const {selectedRowKeys} = this.state;
        if (selectedRowKeys && selectedRowKeys.length) {
            let params = ''
            selectedRowKeys.forEach(row =>
                params += 'ids=' + row + '&'
            )
            params = params.substring(0, params.length - 1)
            const aLink = document.createElement('a');
            document.body.appendChild(aLink);
            aLink.style.display = 'none';
            aLink.download = `梁场二维码.zip`;
            aLink.href = `${BASE_URL}api/ibf/body/qrCode?${params}`;
            aLink.click();
            document.body.removeChild(aLink);
        }
    }
    handleCreate = async () => {
        const {data: { data }} = await api.getUserInfo(localStorage.getItem('beamField'));
        this.setState({
            beamInfo:data,
            visible: true,
            current: {},
            title: '新增台座',
        });
    };
    handleRowSelectChange = (selectedRowKeys, selectedRows) => {
        this.setState({selectCheckList:[]});
        this.setState({selectCheckList:selectedRows});
        const isHasData  = (row)=>{
            if(!row.hasData) {
                return row
            }
            return false; 
        }
        this.setState({
            selectedRowKeys: (selectedRows.filter(isHasData).map(item=>item.id))
        })
        // console.log(selectedRows.filter(isHasData).map(item=>item.id),'selectedRows.filter(isHasData).map(item=>item.id)')
    };
    onChange = (current, size) => {
        this.setState({
            page: current
        }, () => {
            this.getPedestral()
        })
    }

    onShowSizeChange = (page, pageSize) => {
        this.setState({
            size: pageSize,
            page:page
        }, () => {
            this.getPedestral()
        })
    }

    cleanSelectedKeys = () => {
        this.handleRowSelectChange([], []);
    };
    //获取台座列表
    getPedestral = () => {
        this.setState({loading: true});
        const params = {
            "page": this.state.page,
            "size": this.state.size,
        }
        api.getPedestral(params).then(res => {
            res.data.items.forEach((val,key)=>{
                val.handle = val.handle!==''? '已关联':'未关联'
            })
            this.setState({
                pedestalList: res.data.items,
                totalCount: res.data.totalCount,
                loading: false
            })
        })
    }
    //删除台座列表
    deletePedestal = () => {
        const {selectedRowKeys,selectCheckList} = this.state;
        let _this = this;
        if (selectedRowKeys && selectedRowKeys.length) {
            confirm({
                title: selectedRowKeys.length == selectCheckList.length?'是否确认删除选中行?':"是否确认删除选中行中无生产数据的台座？",
                // content: '是否确认删除选中行',
                okText: '取消',
                cancelText: '确认',
                onOk() {
                    console.log('OK');
                },
                onCancel() {
                    api.deletePedestral(selectedRowKeys).then(res => {
                        if (res.code === 1) {
                            message.success('删除成功');
                            _this.getPedestral();
                            _this.cleanSelectedKeys()
                        }
                    })
                },
            });
        }
    }
    //bim刷新
    bimModelrefresh =  ()=> {
        console.log(123)
    }
    components = {
        header: {
            cell: ResizeableTitle,
        },
    };
    handleResize = index => (e, { size }) => {
        this.setState(({ columns }) => {
          const nextColumns = [...columns];
          nextColumns[index] = {
            ...nextColumns[index],
            width: size.width,
          };
          return { columns: nextColumns };
        });
    };
    render() {
        const columns = this.state.columns.map((col, index) => ({
            ...col,
            onHeaderCell: column => ({
              width: column.width,
              onResize: this.handleResize(index),
            }),
        }));
        const rowSelection = {
            onChange: this.handleRowSelectChange
        };
        const {pedestalList, loading, visible, title, current, page, size, textAreaValue, totalCount, codeVisible} = this.state;

        const {areas} = this.state.beamInfo;
        const parentMethods = {
            handleOk: this.handleOk,
            handleCancel: this.handleCancel,
            handleInputValue: this.handleInputValue
        };
        const {setValue,historyInfos} = this.props;
         /* this.props.setValue({areaId:"66656656",stage:"1"}); 
        console.log(this.props.historyInfos,'111111111') */
        const authCode = this.props.authCode.toJS();
        return (
            <Layout>
                <div className='layout-header'>
                    <AuthButton type="primary" source="fun" authcode="77005004" authcodes={authCode.funAuthCode} onClick={this.handleCreate}>新增</AuthButton>
                    <AuthButton type="primary" source="fun" authcode="77005007" authcodes={authCode.funAuthCode} onClick={this.deletePedestal}>删除</AuthButton>
                    <Button type="primary" onClick={this.batchExport}>导出二维码</Button>
                </div>
                <Table
                    rowKey="id"
                    pagination={{
                        current: page,
                        pageSize: size,
                        showSizeChanger: true,
                        showQuickJumper: true,
                        pageSizeOptions: ['20', '30', '50'],
                        onChange: this.onChange,
                        total: totalCount,
                        showTotal: total => (`共${total}条`),
                        onShowSizeChange: this.onShowSizeChange
                    }}
                    loading={loading}
                    scroll={{y: 600}}
                    rowSelection={rowSelection}
                    components={this.components}
                    columns={columns}
                    dataSource={pedestalList}
                    bordered/>
                <ModalForm {...parentMethods}
                           visible={visible}
                           title={title}
                           current={current}
                           textAreaValue={textAreaValue}
                           areas={areas}
                           historyInfos ={historyInfos}
                           setValue = {setValue}></ModalForm>
                <CodeModal handleCancel={this.handleCancel} visible={codeVisible} current={current}></CodeModal>
            </Layout>
        )
    };

    componentDidMount() {
        this.getPedestral()
    }


}

const mapStateToProps = state => {
    return {
        beamInfo: state.getIn(["login", "beamInfo"]),
        historyInfos: state.getIn(["panel", "historyInfos"]),
        authCode: state.getIn(["login", "authCode"]),
    };
};
const mapDispatchToProps = {
    setValue:(itemInfo) => actionCreators.platSettingAddInfo(itemInfo)
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(PlatformSetting);